<template>
  <div class="category-container">
    <div class="content" v-for="item in arr" :key="item.id">
      <div class="title">
        商品类目：<el-tag>{{ item.name }}</el-tag> id:<el-tag>{{
          item.id
        }}</el-tag>
        <el-button
          class="delete"
          size="small"
          type="danger"
          @click="titledelete(item.id)"
          >删除</el-button
        >
        <Router-link :to="{ path: 'editcate' }">
          <el-button class="edit" size="small" @click="editclick(item)"
            >编辑</el-button
          ></Router-link
        >
      </div>
      <div class="subtitle">
        商品子类目：
        <el-tag type="success" v-for="it in item.c_items" :key="it.id">{{
          it
        }}</el-tag>
      </div>
      <el-divider></el-divider>
    </div>
    <div class="floot">
      <el-button class="btn" @click="dialogaddVisible = true" type="success"
        >添加</el-button
      >
      <el-dialog title="添加" :visible.sync="dialogaddVisible">
        <div class="header">
          商品类目：<input
            type="text"
            v-model="addInput"
            class="addtitleinput"
          />
          id:<input type="text" v-model="addid" class="addtitleinput" />
        </div>
        <div class="bianji">
          商品子类目：
          <el-tag
            :key="tag"
            v-for="tag in dynamicTags"
            closable
            :disable-transitions="false"
            @close="handleClose(tag)"
          >
            {{ tag }}
          </el-tag>
          <el-input
            class="input-new-tag"
            v-if="inputVisible"
            v-model="inputValue"
            ref="saveTagInput"
            size="small"
            @keyup.enter.native="handleInputConfirm"
            @blur="handleInputConfirm"
          >
          </el-input>
          <el-button
            v-else
            class="button-new-tag"
            size="small"
            @click="showInput"
            >+ 商品子类目</el-button
          >
        </div>
        <el-button
          type="success"
          :style="{ 'margin-left': '80%', 'margin-top': '10px' }"
          @click="addSubmit"
          >提交</el-button
        >
      </el-dialog>
    </div>
  </div>
</template>

<script>
import api from "@/api/product.js";
export default {
  data() {
    return {
      arr: [],
      dialogtitleVisible: false,
      dialogaddVisible: false,
      dynamicTags: [],
      inputVisible: false,
      inputValue: "",
      addInput: "",
      addid: "",
    };
  },
  methods: {
    titledelete(id) {
      api.deleteCategory(id).then((res) => {
        if (res.status == "success") {
          alert("删除成功");
          window.location.reload();
        }
      });
    },
    editclick(item) {
      this.$store.state.editnumber = item;
    },
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    },
    addSubmit() {
      if (this.addInput == "") {
        alert("信息不完整！请继续填写！");
        return;
      }
      api
        .addCategory({
          id: Number(this.addid),
          name: this.addInput,
          c_items: [...this.dynamicTags],
        })
        .then((res) => {
          if (res.status == "success") {
            // this.$router.push({ name: "category" });
            alert("添加成功");
            window.location.reload();
          }
        });
    },
  },
  created() {
    api.categoryList().then((res) => {
      // console.log(res);
      this.arr = res.data.data;
    });
  },
};
</script>

<style scoped lang="less">
.category-container {
  .content {
    width: 60%;
    margin: 0 auto;
    .title {
      margin: 10px 0;
      .el-tag {
        margin: 0 10px;
      }
      .edit,
      .delete {
        float: right;
        margin: 0 20px;
      }
    }
    .subtitle {
      .el-tag {
        margin: 10px;
      }
    }
    .el-divider {
      margin: 16px 0;
    }
  }
  .floot {
    margin: 0 auto 50px;
    width: 60%;
    .btn {
      width: 100%;
    }
    .el-dialog{
      .header{
        input{
          margin-right: 50px;
        }
      }
      .bianji{
        height: 32px;
        line-height: 32px;
      }
    }
    .el-tag + .el-tag {
      margin: 5px 10px;
    }
    .button-new-tag {
      margin-left: 10px;
      height: 32px;
      line-height: 30px;
      padding-top: 0;
      padding-bottom: 0;
    }
    .input-new-tag {
      width: 90px;
      margin-left: 10px;
      vertical-align: bottom;
    }
    .header {
      margin: 10px 0;
      .addtitleinput {
        width: 100px;
      }
    }
  }
}
</style>